html {
  box-sizing: border-box;
  font-size: 18px;
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.5;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #fff;
  color: #222;
}

em,
i {
  font-style: italic;
}

small {
  font-size: 0.75em;
}

big {
  font-size: 1.25em;
}
